<template>
    <v-container id="template">
        <v-btn  id="print" class="blue white--text mr-4 mb-5" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="red white--text mb-5" to="/cetakmwedcbesar" link>Back</v-btn>
        <p id="print">Catatan : <br> *Kertas Print = 8.5 x 13 in <br> *Orientasi = Landscape</p>
        <template id="template">
                    <div class="grid-container">
                        <template v-for="i in karton">    
                            <div v-for="j in i['Jumlah']" :key="j.id" class="grid-item">
                                <table cellspacing="0">
                                    <tr>
                                        <td><i>Ingredients</i></td>
                                    </tr>
                                    <tr height="18px" style="vertical-align: top;">
                                        <td><i>{{ i['Ingredients'] }}</i></td>
                                    </tr>
                                    <tr>
                                        <td><b>Nama Brg : {{ i['Kode Barang'] }}</b></td>
                                    </tr>
                                    <tr>
                                        <td><b>No Bets : {{ i['No Bets'] }} </b></td>
                                    </tr>
                                    <tr>
                                        <td><b>Exp Date : {{ i['Expired Date'] }}</b></td>
                                    </tr>
                                </table>
                                <table cellspacing="0" style="margin-left:0.2cm;border-spacing: 4px 0.001px;">
                                    <tr>
                                        <td>
                                            <img :src="$publicUrl + i['path']" alt="Barcode" style="width:2.7cm;height:0.501cm">
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Netto 80 ml </td>
                                    </tr>
                                    <tr>
                                        <td><i>Diproduksi Oleh :</i></td>
                                    </tr>
                                    <tr>
                                        <td><i>PT. Niagara Kosmetik</i></td>
                                    </tr>
                                    <tr>
                                        <td><i>Jl. Pleret Raya No. 5-7 Sumber</i></td>
                                    </tr>
                                    <tr>
                                        <td style="height:0.228cm"><i>Solo - Indonesia</i></td>
                                    </tr>
                                </table>
                            </div>
                        </template>
                    </div>
        </template>
    </v-container>
</template>
<script>
import VueBarcode from 'vue-barcode';
    export default {
        components: {
            'barcode': VueBarcode
        },
        data() {
            return {
                karton: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'cetakmwedcbesar/cetak'
                this.$http.get(uri).then(response => {
                    this.karton = response.data
                    console.log(this.karton)
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
            display: none;
        }

        #template {
            background-color: white
        }
      
    }
   .grid-container{
            column-gap: 0.2cm;
            /* row-gap: 0.42cm; */
            row-gap: 0.42cm;
            display: grid;
            grid-template-columns: repeat(5, 6cm);
            font-size: 5pt;
            font-family: "Times New Roman", Times, serif;
            text-align: center;
            margin-top: 0.52cm;
            /* margin-bottom: 6mm; */
            margin-left: 14.2mm;
            margin-right: 6mm;
        }

        .grid-item{
            height: 1.594cm;
            width: 5.4cm;
            display: flex;
        }
</style>